.login-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;width:100%}.login-wrapper .login-content{background-color:#fff;box-shadow:0 4px 24px 0 #d4d2d2;padding:30px 30px;border-radius:20px}.login-wrapper .login-content__header{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:20px;margin-bottom:10px}.login-wrapper .login-content__header-img .logo-img{width:100%;height:auto;object-fit:contain}.login-wrapper .login-content__header-title h2{color:#fb960b;font-weight:600;margin-bottom:0;font-family:"Times New Roman", Times, serif}.login-wrapper .login-content__description h3{font-family:inherit;font-weight:500;line-height:1.2;color:#5e5873;font-size:18px;font-family:"Times New Roman", Times, serif}.login-wrapper .login-content__description h4{font-family:inherit;font-weight:500;line-height:1.2;color:#6e6b7b;font-size:14px;font-family:"Times New Roman", Times, serif}.login-wrapper .login-content__form .box-form-login .ant-form-item-label label{font-style:normal;font-weight:400;font-size:16px !important;line-height:18px;color:#5e5873;font-weight:700;font-family:"Times New Roman", Times, serif}.login-wrapper .login-content__form .box-form-login .ant-form-item-control-input-content .input-field{height:58px;border-radius:6px}.login-wrapper .login-content__form .box-form-login .ant-form-item-control-input-content input{font-style:normal;font-weight:400;font-size:16px;line-height:18px;color:#5e5873;font-weight:700;font-family:"Times New Roman", Times, serif}.login-wrapper .login-content__form .box-form-login .submit-form{height:40px;border-radius:6px;border-color:#fb960b;background-color:#fb960b;width:100%;transition:ease-in-out 0.3s all}.login-wrapper .login-content__form .box-form-login .submit-form:hover{transform:scale(1.05);transition:ease-in-out 0.3s all}.login-wrapper .login-content__form .box-form-login .submit-form.disable{opacity:0.8}.login-wrapper .login-content__form .box-form-login .submit-form span{font-style:normal;font-weight:400;font-size:16px;line-height:18px;color:#fff;text-transform:uppercase;font-weight:700;font-family:"Times New Roman", Times, serif}
